import React, { useContext } from 'react';
import Helmet from 'react-helmet';

import { CustomListContext } from 'App';

import LineChart from 'components/lineChart';
import { PlayerProfile, ThisWeek } from 'components/random';

export function CustomList({ position }) {
  const { customList } = useContext(CustomListContext);

  return (
    <div className="container">
      <div style={{ textAlign: 'center' }}>
        <h1>Custom List</h1>
        {MetaData({
          caption: 'DK Charts',
          position: position,
        })}
      </div>
      <div>
        {customList.map((item, index) => {
          return (
            <div className="grid-3-5" key={item.playerDkId}>
              <div>
                <PlayerProfile
                  avatar={item.avatar}
                  displayName={item.firstName + ' ' + item.lastName}
                  position={item.position}
                  team={item.team}
                  player={item}
                />
                <div style={{ marginBottom: '.667rem' }}></div>
                <ThisWeek week={item.thisWeek} />
              </div>

              <div>
                <LineChart
                  salary={item.salary}
                  projected={item.projected}
                  projectedWin={item.projectedWin}
                  points={slicePoints(item.points)}
                  opponent={item.opponent}
                  receptions={item.receptions}
                  targets={item.targets}
                />
              </div>
            </div>
          );
        })}
      </div>
    </div>
  );
}

export default CustomList;

function slicePoints(array) {
  if (array && array.length) {
    return array.filter((week) => week.x !== process.env.REACT_APP_WEEK);
  }

  return [];
}

function MetaData({ caption, position }) {
  const title = caption;
  const url = 'https://dk.mattlovan.com/positon/' + position;
  const description = caption;
  const image = 'https://dk.mattlovan.com/logo_share.png';

  return (
    <Helmet>
      <title>{title}</title>
      <link rel="canonical" href={url} />
      <meta name="description" content={description} />

      <meta property="og:site_name" content={title} />
      <meta property="og:type" content="website" />
      <meta property="og:url" content={url} />
      <meta property="og:image:secure_url" content={url} />
      <meta property="og:image" content={image} />
      <meta property="og:image:type" content="png" />
      <meta property="og:image:height" content="201" />
      <meta property="og:image:width" content="630" />

      <meta name="twitter:card" content="summary_large_image" />
      <meta name="twitter:site" content="Dk Charts" />
      <meta name="twitter:title" content={title} />
      <meta name="twitter:description" content={description} />
      <meta name="twitter:image" content={image} />
    </Helmet>
  );
}
