import React, { useState, useEffect } from 'react';
import Helmet from 'react-helmet';

import LineChart from 'components/lineChart';
import { PlayerProfile, Bouncing, ThisWeek } from 'components/random';

const captions = {
  qb: {
    title: 'Quarterbacks',
    description: '',
  },
  rb: {
    title: 'Running backs',
    description: '',
  },
  wr: {
    title: 'Wide receivers',
    description: '',
  },
  te: {
    title: 'Tight ends',
    description: '',
  },
  dst: {
    title: 'Defenses',
    description: '',
  },
};

export function PositionList({ position }) {
  const [loading, setLoading] = useState(false);
  const [errorMessage, setErrorMessage] = useState(null);

  const [playerList, setPlayerList] = useState([]);

  // get latest week
  useEffect(() => {
    const fetchData = async () => {
      setLoading(true);
      setErrorMessage(null);
      const baseUrl = '/api/position/' + position;
      try {
        const response = await fetch(baseUrl);
        if (response.status !== 200) throw Error(response.status);
        const data = await response.json();
        setPlayerList(data.playerData);
        setLoading(false);
      } catch (error) {
        console.log(error);
        setErrorMessage(error.toString());
        setLoading(false);
      }
    };

    fetchData();
  }, [position]);

  return (
    <div className="container">
      <div style={{ textAlign: 'center' }}>
        <h1>{captions[position].title}</h1>
        {loading ? <Bouncing /> : null}
        {errorMessage ? <p>{errorMessage}</p> : null}
      </div>
      <div>
        {!loading ? (
          <React.Fragment>
            {playerList.map((player, index) => {
              return (
                <div
                  className="grid-3-5"
                  style={{ paddingBottom: '1rem' }}
                  key={player.playerDkId}
                >
                  <div>
                    <PlayerProfile
                      avatar={player.avatar}
                      displayName={player.firstName + ' ' + player.lastName}
                      position={player.position}
                      team={player.team}
                      player={player}
                    />
                    <div style={{ marginBottom: '.667rem' }}></div>
                    <ThisWeek week={player.thisWeek} />
                  </div>
                  <div>
                    <LineChart
                      salary={player.salary}
                      projected={player.projected}
                      projectedWin={player.projectedWin}
                      points={slicePoints(player.points)}
                      opponent={player.opponent}
                      receptions={player.receptions}
                      targets={player.targets}
                    />
                  </div>
                </div>
              );
            })}
          </React.Fragment>
        ) : null}
      </div>
      {MetaData({
        caption: `${captions[position].title} | DK Charts`,
        position: position,
      })}
    </div>
  );
}

export default PositionList;

function slicePoints(array) {
  if (array && array.length) {
    return array.filter((week) => week.x !== process.env.REACT_APP_WEEK);
  }

  return [];
}

function MetaData({ caption, position }) {
  const title = caption;
  const url = 'https://dk.mattlovan.com/positon/' + position;
  const description = caption;
  const image = 'https://dk.mattlovan.com/logo_share.png';

  return (
    <Helmet>
      <title>{title}</title>
      <link rel="canonical" href={url} />
      <meta name="description" content={description} />

      <meta property="og:site_name" content={title} />
      <meta property="og:type" content="website" />
      <meta property="og:url" content={url} />
      <meta property="og:image:secure_url" content={url} />
      <meta property="og:image" content={image} />
      <meta property="og:image:type" content="png" />
      <meta property="og:image:height" content="201" />
      <meta property="og:image:width" content="630" />

      <meta name="twitter:card" content="summary_large_image" />
      <meta name="twitter:site" content="Dk Charts" />
      <meta name="twitter:title" content={title} />
      <meta name="twitter:description" content={description} />
      <meta name="twitter:image" content={image} />
    </Helmet>
  );
}
