import React from 'react';

import { Router, Link } from '@reach/router';
import { OnRouteChange } from 'routingHack.js';
import { buildTime } from 'components/random';

// routes
import Header from './components/header';

import LandingPage from './components/LandingPage';
import Analysis from './components/Analysis';
import { WeekForm as Charts } from './components/WeeklyChart';
import PositionList from './components/PositionList';
import PositionScatter from './components/positionScatter';
import Player from './components/Player';
import CustomList from './components/CustomList';
import NotFound from './components/404';

// Custom List
export const CustomListContext = React.createContext({});
const customList = [];
function addToList(player) {
  customList.push(player);
}
function removeFromList(index) {
  customList.splice(index, 1);
}
function toggleSelected(player) {
  const index = customList.findIndex((x) => x.playerDkId === player.playerDkId);

  if (index >= 0) {
    // console.log('remove');
    removeFromList(index);
  } else {
    // console.log('add');
    addToList(player);
  }
  // console.log('toggle', index, customList);
}
function onList(player) {
  return customList.findIndex((x) => x.playerDkId === player.playerDkId) >= 0;
}

const version = process.env.REACT_APP_VERSION;
function App() {
  return (
    <CustomListContext.Provider value={{ customList, onList, toggleSelected }}>
      <div className="App">
        <Header />
        <div className="page-container">
          <Router>
            <LandingPage path="/" />
            <Charts path="/charts" />
            <Player path="/player/:playerDkId" />
            <PositionList path="/position/:position" />
            <PositionScatter path="/scatter/:week" />
            <CustomList path="/custom" />

            <NotFound default />
          </Router>
          <OnRouteChange
            action={() => {
              window.scrollTo(0, 0);
            }}
          ></OnRouteChange>
        </div>

        <footer>
          <p>
            v{version} ({buildTime()})
          </p>
        </footer>
      </div>
    </CustomListContext.Provider>
  );
}

export default App;
