import React from 'react';

import {
  VictoryTheme,
  VictoryChart,
  VictoryLine,
  VictoryBar,
  VictoryLabel,
  VictoryScatter,
  VictoryAxis,
  VictoryLegend,
} from 'victory';

function LineChart({
  week = parseInt(process.env.REACT_APP_WEEK),
  points = [],
  projected = [],
  projectedWin = [],
  opponent = [],
  targets = [],
}) {
  const domainArray = Array.from({ length: week + 1 }, (_, i) => i + 1);

  return (
    <VictoryChart
      theme={VictoryTheme.material}
      height={150}
      domainPadding={{ x: 0, y: 10 }}
      padding={{ top: 18, bottom: 18, left: 35, right: 35 }}
    >
      <svg>
        <defs>
          <linearGradient id="myGradient" x1="0%" y1="0%" x2="0%" y2="100%">
            <stop offset="0%" stopColor="green" />
            <stop offset="100%" stopColor="red" />
          </linearGradient>
        </defs>
      </svg>
      {/* stroke: 'url(#myGradient)', */}

      <VictoryLegend
        orientation="horizontal"
        centerLabel
        x={35}
        gutter={15}
        style={{
          // border: { stroke: 'black' },
          labels: { fontSize: 6, fontFamily: 'Montserrat' },
        }}
        data={[
          { name: '2.5X (125 pts)', symbol: { fill: '#aaaaaa80' } },
          { name: '3.5X (175 pts)', symbol: { fill: '#6DCE1280' } },
          { name: 'Points Scored', symbol: { fill: '#00A7F5' } },
          { name: 'Targets', symbol: { fill: '#00A7F540' } },
        ]}
      />

      <VictoryAxis
        domain={[1, week + 1]}
        tickValues={domainArray}
        tickFormat={(tick) => {
          const weekIndex = opponent.findIndex((object) => object.x === tick);
          if (weekIndex > -1) {
            return opponent[weekIndex].label;
          }

          return '';
        }}
        style={{
          axis: { stroke: '#aaa', strokeWidth: 1 },
          ticks: { stroke: '#ddd', size: 3 },
          tickLabels: {
            fontSize: 6,
            fontFamily: 'Montserrat',
            fontWeight: 'bold',
            padding: 4,
            fill: '#888',
          },
        }}
      />

      <VictoryAxis
        dependentAxis
        tickFormat={(x) => `${x / 1000} pts`}
        theme={VictoryTheme.material}
        style={{
          axis: { stroke: '#aaa', strokeWidth: 1 },
          tickLabels: {
            fontSize: 6,
            fontFamily: 'Montserrat',
            padding: 4,
            fill: '#888',
          },
        }}
      />

      {/* Lines */}
      <VictoryLine
        data={projected}
        interpolation="natural"
        style={{
          data: {
            stroke: '#aaaaaa80',
            strokeDasharray: '3',
            strokeWidth: 2,
          },
        }}
      />
      <VictoryLine
        data={projectedWin}
        interpolation="natural"
        style={{
          data: {
            stroke: '#6DCE1280',
            strokeDasharray: '4',
            strokeWidth: 2,
          },
        }}
      />

      {/* Targets */}
      <VictoryBar
        data={targets}
        labels={({ datum }) => datum.y / 1000}
        labelComponent={<VictoryLabel dy={-2} />}
        barRatio={0.4}
        style={{
          data: {
            fill: '#00A7F540',
            stroke: '#00A7F540',
            strokeWidth: 0,
          },
          labels: {
            fontSize: 5,
            fontFamily: 'Montserrat',
            fill: '#00A7F5',
          },
        }}
      />

      {/* Points */}
      <VictoryScatter
        symbol="circle"
        size={2}
        style={{ data: { fill: '#00A7F5' } }}
        data={points}
      />
      <VictoryLine
        data={points}
        labels={({ datum }) => datum.y / 1000}
        style={{
          data: {
            stroke: '#00A7F5',
            strokeWidth: 1,
          },
          labels: {
            fontSize: 5,
            fontFamily: 'Montserrat',
            fill: '#007DB8',
          },
        }}
      />
    </VictoryChart>
  );
}

export default LineChart;

