import React, { useState, useEffect } from 'react';
import Helmet from 'react-helmet';

import LineChart from 'components/lineChart';
import { PlayerProfile } from 'components/random';

export function WeekForm({ playerDkId }) {
  // const { callApi, user, updateUser } = useContext(AuthContext);

  const [loading, setLoading] = useState(false);
  const [errorMessage, setErrorMessage] = useState(null);
  // const [successMessage, setSuccessMessage] = useState(null);

  const [player, setPlayer] = useState({});

  // get latest week
  useEffect(() => {
    const fetchData = async () => {
      setLoading(true);
      const baseUrl = '/api/player/' + playerDkId;
      try {
        const response = await fetch(baseUrl);
        const data = await response.json();
        setPlayer(data);
        setLoading(false);
      } catch (error) {
        console.log(error);
        setErrorMessage(error.toString());
        setLoading(false);
      }
    };

    fetchData();
  }, [playerDkId]);

  return (
    <div className="container">
      <div style={{ textAlign: 'center' }}>
        {MetaData(player)}
        {loading ? <Bouncing /> : null}
        {errorMessage ? <p>{errorMessage}</p> : null}
      </div>
      {!loading ? (
        <div>
          <div style={{ marginBottom: '1rem' }}></div>
          <PlayerProfile avatar={player.avatar} player={player} />

          <LineChart
            salary={player.salary}
            projected={player.projected}
            projectedWin={player.projectedWin}
            points={slicePoints(player.points)}
            opponent={player.opponent}
            receptions={player.receptions}
            targets={player.targets}
          />
        </div>
      ) : null}
    </div>
  );
}

export default WeekForm;

function slicePoints(array) {
  if (array && array.length) {
    return array.filter((week) => week.x !== process.env.REACT_APP_WEEK);
  }

  return [];
}

export function Bouncing() {
  return (
    <div style={{ textAlign: 'center', display: 'inline-block' }}>
      <div className="spinner">
        <div className="bounce1"></div>
        <div className="bounce2"></div>
        <div className="bounce3"></div>
      </div>
    </div>
  );
}

function MetaData(player) {
  const title = player.firstName + ' ' + player.lastName;
  const url = 'https://dk.mattlovan.com/player/' + player.playerdkId;
  const description =
    player.firstName + ' ' + player.lastName + ' on DK Charts';
  const image = 'https://dk.mattlovan.com/logo_share.png';

  return (
    <Helmet>
      <title>{title}</title>
      <link rel="canonical" href={url} />
      <meta name="description" content={description} />

      <meta property="og:site_name" content={title} />
      <meta property="og:type" content="website" />
      <meta property="og:url" content={url} />
      <meta property="og:image:secure_url" content={url} />
      <meta property="og:image" content={image} />
      <meta property="og:image:type" content="png" />
      <meta property="og:image:height" content="201" />
      <meta property="og:image:width" content="630" />

      <meta name="twitter:card" content="summary_large_image" />
      <meta name="twitter:site" content="Dk Charts" />
      <meta name="twitter:title" content={title} />
      <meta name="twitter:description" content={description} />
      <meta name="twitter:image" content={image} />
    </Helmet>
  );
}
