import React, { useState, useEffect } from 'react';
import Helmet from 'react-helmet';

import ScatterChart from 'components/scatterChart';
import { Bouncing } from 'components/random';

export function PositonScatter({ week }) {
  // const { callApi, user, updateUser } = useContext(AuthContext);

  const [loading, setLoading] = useState(false);
  const [errorMessage, setErrorMessage] = useState(null);
  // const [successMessage, setSuccessMessage] = useState(null);

  const [array, setArray] = useState([]);

  // get latest week
  useEffect(() => {
    const fetchData = async () => {
      setLoading(true);
      const baseUrl = '/api/week/' + week;
      try {
        const response = await fetch(baseUrl);
        const data = await response.json();

        const salaryValue = data.map((player) => {
          return {
            x: player.salary,
            y: player.salaryMultiple,
            position: player.position,
          };
        });

        // console.log(salaryValue);
        setArray(salaryValue);
        setLoading(false);
      } catch (error) {
        console.log(error);
        setErrorMessage(error.toString());
        setLoading(false);
      }
    };

    fetchData();
  }, [week]);

  return (
    <div className="container">
      <div style={{ textAlign: 'center' }}>
        <h1>Week {week}</h1>
        {/* {MetaData(player)} */}
        {loading ? <Bouncing /> : null}
        {errorMessage ? <p>{errorMessage}</p> : null}
      </div>
      {!loading ? (
        <div>
          <div style={{ marginBottom: '1rem' }}></div>

          <ScatterChart salaryValue={array} />
        </div>
      ) : null}
    </div>
  );
}

export default PositonScatter;

